@import url(https://fonts.googleapis.com/css?family=Fira+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0;padding:0;box-sizing:border-box;background:#e9e9e9;font-family:"Fira Mono", monospace, sans-serif;font-size:20px}body{height:100vh;width:100vw;display:flex;align-items:center;justify-content:center}main{box-shadow:-6px -6px 9px #fff,6px 6px 9px rgba(0,0,0,0.4);padding:1.5rem;border-radius:30px}main input,main button{outline:none}main input::-webkit-outer-spin-button,main input::-webkit-inner-spin-button{-webkit-appearance:none}main input[type=number]{-moz-appearance:textfield}main .screen{box-shadow:inset -6px -6px 9px #fff,inset 6px 6px 9px rgba(0,0,0,0.4);width:320px;height:80px;padding:16px 32px;text-align:right;border:none;border-radius:30px;text-shadow:-1px -1px 2px #fff,2px 2px 2px rgba(0,0,0,0.4)}main .keypad__row{height:80px;width:100%;display:flex;color:white;align-items:center}main .keypad{height:400px;width:100%}main .btn{box-shadow:-6px -6px 9px #fff,6px 6px 9px rgba(0,0,0,0.4);margin:auto;width:60px;height:60px;border:none;border-radius:30px;cursor:pointer;background:e9e9e9}main .btn:hover,main .btn:active{background:#ccc}main .btn:active{box-shadow:inset -6px -6px 9px #fff,inset 6px 6px 9px rgba(0,0,0,0.4)}main .btn--primary{color:#d40640}main .btn--operator{color:#ff652f}main .btn--large{color:#ff3c00;width:120px}main .computation-screen{color:gray}

